<template>
    <div id="number-game">
        <div v-if="!checkAnswer">
            <van-nav-bar left-arrow :right-text="!isAnswering?'记好了':'提交'" @click-left="onClickNavLeft" @click-right="onClickNavRight">
                <template #title>
                    <van-row>
                        <van-icon name="clock-o" size="18" />
                        <!-- <van-count-down v-if="countDown > 0" :time="countDown" format="HH:mm:ss" @finish="onCountDownFinish" @change="countDownChanged"/> -->
                        <div>{{parseInt(countDown/60)}}分{{countDown%60}}秒</div>
                    </van-row>
                </template>
            </van-nav-bar>
            <div class="question" v-if="!isAnswering">
                <div v-for="(num, index) in question" class="tag-item" :key="index">{{ num }}</div>
            </div>
            <div v-else class="question">
                <div v-for="(num, index) in answer" class="tag-item" :class="this.currentIndex===index?'tag-item-selected':''" :key="index" @click="selectNum(index)">{{ num }}</div>
            </div>
        </div>
        <div v-else>
            <van-nav-bar left-arrow @click-left="onClickNavLeft" right-text="再来一次" title="查看答案" @click-right="gameAgain">
                
            </van-nav-bar>
            <div>题目</div>
            <div class="question">
                <div v-for="(num, index) in question" class="tag-item" :key="index">{{ num }}</div>
            </div>
            <div>答案</div>
            <div class="question">
                <div v-for="(num, index) in answer" class="tag-item" :class="num!==question[index]?'tag-item-error':''" :key="index" @click="selectNum(index)">{{ num }}</div>
            </div>
        </div>
        <van-number-keyboard
            :show="isAnswering && !checkAnswer"
            theme="custom"
            :extra-key="['←', '→']"
            close-button-text="完成"
            @input="onInput"
            @delete="onDelete"
        />
        <van-dialog 
            v-model:show="showResult" 
            title="游戏结果" 
            show-cancel-button 
            confirmButtonText="再来一次"
            @confirm="gameAgain"
            cancelButtonText="查看答案"
            @cancel="checkRightAnswer"
        >
            <van-cell>总数量：{{count}}</van-cell>
            <van-cell>正确：{{rightCount}}</van-cell>
            <van-cell>错误：{{count - rightCount}}</van-cell>
            <van-cell>总共用时：{{parseInt(useTime/60)}}分{{useTime%60}}秒</van-cell>
        </van-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            answer: [],
            question: [],
            count: 0,
            time: 0,
            countDown: 0,
            inputText: "",
            isAnswering: false,
            currentIndex: 0,
            showResult: false,              // 显示结果
            rightCount: 0,
            useTime: 0,
            timer: null,
            checkAnswer: false
        };
    },
    methods: {
        onInput(value) {
            if (value === '←') {
                this.currentIndex > 0 && this.currentIndex --;
            } else if (value === '→') {
                (this.currentIndex < this.count - 1) && this.currentIndex ++;
            } else {
                this.answer.splice(this.currentIndex, 1, value);
                if (this.currentIndex < this.count - 1) {
                    this.currentIndex ++;
                }
            }
        },
        onDelete() {
            if (this.currentIndex > 0) {
                this.answer.splice(this.currentIndex - 1, 1, " ");
                this.currentIndex --;
            }
        },
        onClickNavLeft() {
            this.destroyGameInfo();
            this.$router.back();
        },
        onClickNavRight() {
            if (this.isAnswering) {
                // 提交
                this.gameOver();
            } else {
                // 进入回答状态
                this.isAnswering = true;
            }
        },
        selectNum(index) {
            if (this.checkAnswer) {
                return;
            }
            this.currentIndex = index;
            this.isAnswering = true;
        },
        initGame() {
            // 传进来的设定记忆数量和记忆时间
            let { count, time } = this.$route.params;
            this.count = parseInt(count);
            this.time = parseInt(time) * 60;
            this.countDown = this.time;

            // 准备随机数字
            let question = [];
            let answer = [];
            for (let index = 0; index < count; index++) {
                let num = parseInt(Math.random() * 9);
                question.push(num);
                answer.push(" ");
            }
            this.question = question;
            this.answer = answer;
            let that = this;
            this.timer = setInterval(() => {
                if (that.countDown > 0) {
                    that.countDown --;
                } else {
                    that.gameOver();
                }
            }, 1000);
            this.checkAnswer = false;
            this.showResult = false;
            this.isAnswering = false;
            this.currentIndex = 0;
            this.useTime = 0;
            this.rightCount = 0;
        },
        gameOver() {
            this.destroyGameInfo();
            let useTime = this.time - this.countDown;          // 总共用时
            this.useTime = useTime;
            let rightCount = 0;                             // 正确个数
            for (let index = 0; index < this.question.length; index++) {
                let questionChar = this.question[index];
                let answerChar = this.answer[index];
                if (questionChar === answerChar) {
                    rightCount ++;
                }
            }
            this.rightCount = rightCount;
            this.showResult = true;
        },
        gameAgain() {
            this.initGame();
        },
        checkRightAnswer() {
            this.checkAnswer = true;
        },
        destroyGameInfo() {
            if (this.timer) {
                clearInterval(this.timer);
                this.timer = null;
            }
        }
    },
    mounted() {
        this.initGame();
    },
    onUnmounted() {
        debugger
    }
};
</script>

<style lang="less" scoped>
#number-game {
    .question {
        align-items: center;
        display: flex;
        display: -webkit-flex;
        // justify-content: space-between;
        flex-direction: row;
        flex-wrap: wrap;
        .tag-item {
            margin-top: 20px;
            border: 1px solid blue;
            width: calc((100% - 9 * 10px) / 10.0);
            height: 40px;
            border-radius: 4px;
            color: blue;
            font-size: 20px;
            line-height: 40px;
            margin-left: 10px;
            font-weight: bold;
        }
        .tag-item:nth-child(10n + 1) {
            margin-left: 0;
        }
        .tag-item-selected {
            background: blue;
            color: #FFFFFF;
        }
        .tag-item-error {
            background: red;
            border: none;
            color: #FFFFFF;
        }
    }
}
</style>